'use client'
import { useState } from "react";
import AsideShop from "./AsiderShop";

export default function Layout(){
    const [open,setOpen]=useState(false)
    return <aside >
        <button onClick={()=>setOpen(!open)} className="text-xl bg-purple-900 text-violet-300 rounded-r-full flex flex-col gap-1 p-4 transition delay-150 ease-linear  hover:bg-violet-400 hover:w-[100%]">
           Category
        </button>
        {<AsideShop open={open}/>}
    </aside>
}